.matinput {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
  .inner {
    z-index: 1;
    position: relative;
    padding: 1rem;
    width: 100%;
    max-width: 30%;
    height: 60%;
    border-radius: 10px;
    background-color: rgb(255, 255, 255);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: column;
    align-items: center;

    .addmat {
      padding: 1rem 0 1rem 0;
      margin: 1rem 0 1rem 0;
      text-align: center;
      justify-content: center;
      display: flex;
      align-items: center;
      font-size: 1rem;
      font-weight: bolder;
      width: 40%;
    }

    .matlist {
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 1rem 0 0 0;
      border: 1px solid;
      border-radius: 5px;
      box-shadow: 1px 1px 3px 2px rgba(0, 0, 0, 0.3) inset;
      width: 90%;
      height: 60%;
      div {
        display: flex;
        align-items: flex-start;
        justify-content: center;
        width: 95%;
        height: 95%;
        overflow: hidden;
        overflow-y: scroll;
        table {
          width: 100%;
          tr {
            display: flex;
            border-bottom: 1px solid;
            .matname {
              width: 65%;
              display: flex;
              align-items: center;
              justify-content: center;
            }
            .matbtn {
              width: 35%;
              display: flex;
              align-items: center;
              justify-content: center;
              button {
                margin: 0 0.5rem;
                width: 5rem;
              }
            }
          }
        }
      }
    }
    .btn {
      display: flex;
      button {
        margin: 1rem 1rem;
        padding: 0;
        width: 3rem;
        height: 2rem;
        background-color: rgba(128, 128, 128, 0.5);
        box-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
        border-radius: 3px;
        border: 1px solid rgba(114, 107, 107, 0.5);
      }
    }
  }
}
